@import url("https://fonts.googleapis.com/css2?family=Mukta:wght@200&display=swap");

/*Defining base variables*/
:root {
  --accent-purple: #641f6de5;
  --bg-black: rgba(0, 0, 0, 0.904);
  --pink: #ddb1d0;
}

/*Making the home element fit across the entire screen*/
html,
body,
#root,
.home,
.App {
  height: 100%;
  width: 100%;
  margin: 0;
  font-family: "Mukta", sans-serif;
  cursor: none;
}

.home {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: space-between;
}

/*Make the background fit the entire screen*/
body {
  background-color: var(--bg-black);
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  z-index: 1;
}

/*Standard navbar tidying. I also like purple*/
.navBar {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  height: 3em;
  background-color: var(--accent-purple);
  opacity: 0.95;
}

.navBar .navButton {
  width: 10%;
  height: 100%;
  text-align: justify;
}

.navBar .navHome {
  display: flex;
  justify-content: center;
  width: 5%;
  height: 100%;
  margin-right: 1em;
}

.navBar .navButton button {
  color: whitesmoke;
  display: block;
  width: 100%;
  height: 100%;
  border-color: transparent;
  background-color: transparent;
  font-size: 1em;
}

.navButton {
  display: inline-block;
  position: relative;
}

.navButton::after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: var(--pink);
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

/*Underline animation from: https://www.30secondsofcode.org/css/s/hover-underline-animation/*/
.navButton:hover::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

.navBar .navHome a {
  display: flex;
  align-items: center;
  justify-content: center;
}

.navBar .navHome img {
  height: 70%;
}

/*Remove the blue link line under a link element and the pointer*/
.navBar a,
.taskBar a,
.navBar button {
  text-decoration: none;
  cursor: none;
}

/*Styling for the words in the middle; the component 'TypeWriter'*/
.typeWriter h1 {
  display: inline;
}

.typeWriter {
  align-self: center;
  text-align: center;
  display: flex;
  flex-direction: column;
  z-index: 2;
}

.typist {
  color: var(--accent-purple);
  font-size: x-large;
  font-weight: bolder;
}

.taskBar {
  height: 3.8em;
  z-index: 2;
}
.botBar {
  display: inline;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  height: 10%;
  align-self: flex-end;
  z-index: 2;
}

.botBar .botButton img {
  object-fit: scale-down;
  max-height: 2.5em;
  max-width: auto;
  height: auto;
  margin-right: 1.5em;
  background: transparent;
}

.taskBar p {
  display: inline;
  color: rgb(255, 255, 255);
  font-family:
    system-ui,
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    Roboto,
    Oxygen,
    Ubuntu,
    Cantarell,
    "Open Sans",
    "Helvetica Neue",
    sans-serif;
}

.taskBar .link {
  text-decoration: none;
  color: white;
}

.link:hover {
  color: #e6b0eb;
}

/*Animation for hovering over the social media sites 
appropriated from: https://codepen.io/nxworld/pen/ZYNOBZ*/

.botBar .botButton img {
  -webkit-transform: rotate(0) scale(1);
  transform: rotate(0) scale(1);
}
.botBar .botButton:hover img {
  -webkit-transform: rotate(5deg) scale(1.4);
  transform: rotate(5deg) scale(1.4);
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}

/*This css is for the moving cursor animation, adapted from this youtube video: https://www.youtube.com/watch?v=vcQ6_8L-6-Q*/
.cursor {
  position: absolute;
  flex-basis: 0;
  overflow: hidden;
  width: 20px;
  height: 20px;
  border: 2px solid #fce5e5;
  opacity: 0.5;
  border-radius: 50%;
  transition: 0.1s;
  transform: translate(-50%, -50%);
  pointer-events: none;
  z-index: 2;
}
